import { useRef } from 'react';
import { useEffectOnce } from 'react-use';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';

/**
 * @param {object} props
 * @param {MessageEntity} props.message
 */
export function ImageCard({ message }) {
  const imgRef = useRef(/** @type {HTMLImageElement} */ (null));

  useEffectOnce(() => {
    const io = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          const image = /** @type {HTMLImageElement} */ (entry.target);

          image.src = image.dataset['src'];
          image.style.minHeight = 'auto';
          delete image.dataset['src'];

          io.unobserve(entry.target);
          io.disconnect();
        }
      },
      { rootMargin: '900px' },
    );

    io.observe(imgRef.current);

    return () => {
      io.disconnect();
    };
  });

  return (
    <Card elevation={0}>
      <CardMedia
        ref={imgRef}
        component="img"
        referrerPolicy="no-referrer"
        style={{ width: '100%', minHeight: 300 }}
        data-src={message.attachment.url}
        src=""
      />
    </Card>
  );
}
