import { orange, grey } from '@material-ui/core/colors';

export const THEME_TYPE_STORED_KEY = 'xow-theme';

export const getThemeConfig = (/** @type {import('@material-ui/core').PaletteType} */ type) => {
  return /** @type {import('@material-ui/core/styles').ThemeOptions} */ ({
    palette: {
      type,
      primary: {
        main: type === 'light' ? orange['A700'] : grey['900'],
        light: orange['A700'],
        dark: grey['900'],
        contrastText: '#fff',
      },
      secondary: {
        main: type === 'light' ? orange['A400'] : 'rgba(255, 255, 255, 0.7)',
        light: orange['A400'],
        dark: 'rgba(255, 255, 255, 0.7)',
        contrastText: '#000',
      },
      text: {
        primary: type === 'light' ? '#000' : '#fff',
      },
      background: {
        default: type === 'light' ? grey['300'] : grey['700'],
      },
      contrastThreshold: 3,
      tonalOffset: 0.2,
    },
  });
};
