/**
 * ä»æ•°ç»„åˆ›å»ºä¸€ä¸ª EntityCollection
 * @template T
 * @param {T[]} entityList å®ä½“åˆ—è¡¨
 * @param {(t: T) => string} keySelector æä¾›ä¸€ä¸ªå‡½æ•°ï¼Œç¡®å®šæ•°ç»„çš„æ¯ä¸ªå…ƒç´ å¦‚ä½•ç¡®å®šå…¶æ ‡è¯†
 * @returns {EntityCollection<T>}
 */
function createEntityCollection(entityList, keySelector) {
  return {
    list: entityList.map(t => keySelector(t)),
    entities: entityList.reduce((ret, item) => {
      ret[keySelector(item)] = item;
      return ret;
    }, {}),
  };
}

/**
 * å°† EntityCollection é›†åˆè½¬æ¢ä¸ºæ™®é€šæ•°ç»„
 * @template T
 * @param {EntityCollection<T>} collection
 * @returns {T[]}
 */
function convertToEntityList(collection) {
  return collection.list.map(key => collection.entities[key]);
}

module.exports = { createEntityCollection, convertToEntityList };
