import { createEncryptor } from 'simple-encryptor';

const encryptor = createEncryptor({
  key: 'yuer'.repeat(4),
  hmac: false,
  debug: false,
});

const { decrypt } = encryptor;

export const encrypt = obj => {
  while (true) {
    const result = encryptor.encrypt(obj);
    if (['/', '+'].every(c => !result.includes(c))) {
      // remove trailing `equals`
      return result.replace(/=+$/g, '');
    }
  }
};

export { decrypt };
