import { useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActionArea from '@material-ui/core/CardActionArea';
import Grid from '@material-ui/core/Grid';
import Avatar from '@material-ui/core/Avatar';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import RecordVoiceOverIcon from '@material-ui/icons/RecordVoiceOver';
import { dateFormat, moment } from '@src/lib';

const useStyles = makeStyles(theme => ({
  title: {
    margin: '6px auto 4px',
    fontWeight: 'bold',
  },
  avatar: {
    background: theme.palette.background.default,
  },
  description: {
    margin: '4px auto',
  },
}));

/**
 * @param {object} props
 * @param {CourseEntity} props.course
 */
export function CourseCard({ course }) {
  const classes = useStyles();
  const history = useHistory();

  const start = dateFormat(course.started_at, 'yyyy.mm.dd');
  const duration = moment.duration(course.duration).asMinutes();

  return (
    <Card elevation={1}>
      <CardActionArea
        onClick={() => {
          history.push(`/c/${course.fuzzyId}`);
        }}
      >
        <CardContent>
          <Grid container wrap="nowrap" spacing={2}>
            <Grid item>
              <Avatar
                variant="circular"
                src={course.speakers[0].avatar_url}
                imgProps={{ referrerPolicy: 'no-referrer' }}
                className={classes.avatar}
              />
            </Grid>
            <Grid item xs zeroMinWidth>
              <Typography variant="caption" color="textSecondary">
                {[start, `${Math.ceil(duration)} minutes`].join(' Â· ')}
              </Typography>
              <Divider />
              <Typography display="block" noWrap className={classes.title}>
                {course.title}
              </Typography>
              <Typography
                display="block"
                variant="caption"
                color="textSecondary"
                className={classes.description}
              >
                {course.description || 'â†©ï¸'}
              </Typography>
              <Grid container direction="row" alignItems="center" spacing={1}>
                <Grid item>
                  <RecordVoiceOverIcon fontSize="small" color="disabled" />
                </Grid>
                <Grid item>
                  <Typography variant="caption" noWrap color="textSecondary">
                    {course.speakers[0].full_name}
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
