import { useMemo } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import { courseCollection } from '@src/configs/course';
import { coursePhaseCollection } from '@src/configs/course';
import { convertToEntityList } from '@src/lib';
import { useGlobalState } from '@src/components';
import { CourseCard } from './CourseCard';

const { entities: coursePhaseEntites } = coursePhaseCollection;

const useStyles = makeStyles(theme => ({
  root: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
}));

export function CourseList() {
  const classes = useStyles();
  const [globalState] = useGlobalState();
  const courseEntityList = useMemo(() => {
    return convertToEntityList({
      entities: courseCollection.entities,
      list: coursePhaseEntites[globalState.coursePhase].courseIds.map(String),
    });
  }, [globalState.coursePhase]);

  return (
    <Grid container spacing={2} alignItems="stretch" className={classes.root}>
      {courseEntityList.map(course => (
        <Grid key={course.id} item xs={12} sm={6}>
          <CourseCard course={course} />
        </Grid>
      ))}
    </Grid>
  );
}
