export const localStore = {
  /**
   * @param {string} key
   */
  get(key) {
    try {
      return JSON.parse(localStorage.getItem(key));
    } catch (err) {
      return undefined;
    }
  },

  /**
   * @param {string} key
   * @param {any} value
   */
  set(key, value) {
    try {
      localStorage.setItem(key, JSON.stringify(value));
      return true;
    } catch (err) {
      return false;
    }
  },

  /**
   * @param {string} key
   */
  del(key) {
    try {
      localStorage.removeItem(key);
      return true;
    } catch (err) {
      return false;
    }
  },

  isSupport() {
    try {
      const whatever = 'whatever';
      localStorage.setItem(whatever, whatever);
      localStorage.removeItem(whatever);
      return true;
    } catch (err) {
      return false;
    }
  },
};
