import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { dateFormat, moment } from '@src/lib';

const useStyles = makeStyles(theme => ({
  heading: {
    'margin-top': theme.spacing(1),
    'margin-bottom': theme.spacing(1),
    '& > h6': {
      'font-weight': 'bold',
    },
  },
}));

/**
 * @param {object} props
 * @param {CourseEntity} props.course
 */
export function MessageHeading({ course }) {
  const classes = useStyles();

  const start = dateFormat(course.started_at, 'yyyy.mm.dd');
  const duration = moment.duration(course.duration).asMinutes();

  return (
    <div className={classes.heading}>
      <Typography variant="h6" display="block">
        {course.title}
      </Typography>
      {Boolean(course.description) && (
        <Typography variant="caption" display="block" color="textSecondary">
          {course.description}
        </Typography>
      )}
      <Typography variant="caption" display="block" color="textSecondary">
        {[start, `${Math.ceil(duration)} minutes`].join(' Â· ')}
      </Typography>
    </div>
  );
}
