import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import Typography from '@material-ui/core/Typography';
import { prettyBytes } from '@src/lib';

const useStyles = makeStyles(() => ({
  link: {
    '&:hover': {
      'text-decoration': 'none',
    },
  },
}));

/**
 * @param {object} props
 * @param {MessageEntity} props.message
 */
export function PDFCard({ message }) {
  const classes = useStyles();

  return (
    <Card elevation={0}>
      <CardActionArea>
        <CardContent>
          <Link
            href={message.attachment?.url}
            target="_blank"
            rel="noreferrer"
            className={classes.link}
          >
            <Grid container wrap="nowrap" spacing={2} alignItems="center">
              <Grid item>
                <PictureAsPdfIcon fontSize="default" color="error" />
              </Grid>
              <Grid item xs zeroMinWidth>
                <Typography variant="inherit" display="block" color="error">
                  {message.attachment?.name || `${message.attachment?.id}.pdf`}
                </Typography>
                <Typography variant="caption" display="block" noWrap color="textSecondary">
                  {prettyBytes(message.attachment?.size)}
                </Typography>
              </Grid>
            </Grid>
          </Link>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
