import { useMemo, useEffect, useCallback } from 'react';
import { useMount, useUnmount } from 'react-use';
import { soundManager } from 'soundmanager2';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import { EE } from '@src/utils';
import { useGlobalState } from '@src/components';
import { ImageCard } from './ImageCard';
import { AudioCard } from './AudioCard';
import { TextCard } from './TextCard';
import { PDFCard } from './PDFCard';
import { VideoCard } from './VideoCard';

const useStyles = makeStyles(theme => ({
  root: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
}));

/**
 * @param {object} props
 * @param {MessageEntity[]} props.messageList
 */
export function MessageList({ messageList }) {
  const classes = useStyles();
  const [globalState, dispathAction] = useGlobalState();

  useMount(() => {
    // @see https://github.com/leoasis/react-sound
    soundManager.setup({ ignoreMobileRestrictions: true });
  });

  useUnmount(() => {
    dispathAction({ type: 'switchPlayingMediaId', payload: { id: null } });
  });

  const knownMessgeList = useMemo(() => {
    return messageList.filter(x => {
      if (['PLAIN_IMAGE', 'PLAIN_AUDIO', 'PLAIN_TEXT', 'PLAIN_VIDEO'].includes(x.category)) {
        return true;
      } else if (x.category === 'PLAIN_DATA') {
        return ['application/pdf', 'audio/mp3', 'video/mp4'].includes(x.attachment?.mime_type);
      } else {
        return false;
      }
    });
  }, [messageList]);

  const audioMessageList = useMemo(() => {
    return messageList.filter(x => {
      return (
        x.category === 'PLAIN_AUDIO' ||
        (x.category === 'PLAIN_DATA' && x.attachment?.mime_type === 'audio/mp3')
      );
    });
  }, [messageList]);

  const replyNextPlayingMediaId = useCallback(() => {
    const currentIndex = audioMessageList.findIndex(
      x => x.attachment?.id === globalState.playingMediaId,
    );

    // æ²¡æœ‰ä¸‹ä¸€éŸ³é¢‘
    if (currentIndex + 1 >= audioMessageList.length) {
      dispathAction({ type: 'switchPlayingMediaId', payload: { id: null } });
      return;
    }

    const nextPlayingMediaId = audioMessageList[currentIndex + 1]?.attachment?.id;
    EE.emit('replyNextPlayingMediaId', { id: nextPlayingMediaId });
  }, [globalState.playingMediaId, dispathAction, audioMessageList]);

  useEffect(() => {
    EE.on('requestNextPlayingMediaId', replyNextPlayingMediaId);

    return () => {
      EE.off('requestNextPlayingMediaId', replyNextPlayingMediaId);
    };
  }, [replyNextPlayingMediaId]);

  return (
    <Grid container spacing={1} className={classes.root}>
      {knownMessgeList.map(message => (
        <Grid key={message.message_id} item xs={12} sm={12}>
          {message.category === 'PLAIN_IMAGE' && <ImageCard message={message} />}
          {message.category === 'PLAIN_AUDIO' && <AudioCard message={message} />}
          {message.category === 'PLAIN_TEXT' && <TextCard message={message} />}
          {message.category === 'PLAIN_VIDEO' && <VideoCard message={message} />}
          {message.category === 'PLAIN_DATA' && (
            <>
              {message.attachment.mime_type === 'application/pdf' && <PDFCard message={message} />}
              {message.attachment.mime_type === 'audio/mp3' && <AudioCard message={message} />}
              {message.attachment.mime_type === 'video/mp4' && <VideoCard message={message} />}
            </>
          )}
        </Grid>
      ))}
    </Grid>
  );
}
