import { useMemo } from 'react';
import { marked } from '@src/lib';
import { renderToStaticMarkup } from 'react-dom/server.browser';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';

const useStyles = makeStyles(theme => ({
  content: {
    'paddingTop': '0px !important',
    'paddingBottom': '0px !important',
    'wordBreak': 'break-all',

    '& ul': {
      paddingLeft: 16,
    },
  },

  link: {
    '&:hover': {
      'text-decoration': 'none',
    },
  },
}));

/**
 * @param {object} props
 * @param {MessageEntity} props.message
 */
export function TextCard({ message }) {
  const classes = useStyles();

  const html = useMemo(() => {
    const renderer = new marked.Renderer();

    renderer.link = (href, title, text) => {
      return renderToStaticMarkup(
        <Link href={href} title={title} target="_blank" rel="noreferrer" className={classes.link}>
          <Typography variant="inherit" display="inline" color="error">
            {text}
          </Typography>
        </Link>,
      );
    };

    return marked(message.text, {
      gfm: true,
      mangle: true,
      renderer,
    });
  }, [message, classes.link]);

  return (
    <Card elevation={0}>
      <CardContent className={classes.content}>
        <div dangerouslySetInnerHTML={{ __html: html }} />
      </CardContent>
    </Card>
  );
}
