import { useCallback, useMemo, useState } from 'react';
import { useMount } from 'react-use';
import { useTheme } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Badge from '@material-ui/core/Badge';
import IconButton from '@material-ui/core/IconButton';
import SlowMotionVideoIcon from '@material-ui/icons/SlowMotionVideo';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import { convertToEntityList } from '@src/lib';
import {
  playbackRateCollection,
  DEFAULT_PLAYBACK_RATE,
  PLAYBACK_RATE_STORED_KEY,
} from '@src/configs/pbr';
import { useGlobalState } from '@src/components';
import { localStore } from '@src/utils';

const { entities: pbrEntites } = playbackRateCollection;

const useStyles = makeStyles(() => ({
  icon: {
    minWidth: 'auto',
  },
}));

export function PlaybackRateSwitch() {
  const theme = useTheme();
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const [globalState, dispathAction] = useGlobalState();

  const updatePlaybackRate = useCallback(
    (/** @type {number} */ playbackRate) => {
      dispathAction({
        type: 'updatePlaybackRate',
        payload: { value: playbackRate },
      });
    },
    [dispathAction],
  );

  const listItems = useMemo(() => {
    const playbackRateEntityList = convertToEntityList(playbackRateCollection);

    return (
      <List
        component="div"
        subheader={<ListSubheader component="div">{'éŸ³é¢‘æ’­æ”¾å€é€Ÿ'}</ListSubheader>}
      >
        {playbackRateEntityList.map(pbr => {
          const selected = pbr.value === globalState.playbackRate;

          return (
            <ListItem
              key={pbr.value}
              button
              selected={selected}
              onClick={() => {
                updatePlaybackRate(pbr.value);
                setOpen(false);
              }}
            >
              <ListItemText
                primary={
                  <Typography
                    color={
                      selected
                        ? theme.palette.type === 'light'
                          ? 'primary'
                          : 'textPrimary'
                        : theme.palette.type === 'light'
                        ? 'inherit'
                        : 'textSecondary'
                    }
                  >
                    {pbr.text}
                  </Typography>
                }
              />
              <ListItemIcon className={classes.icon}>
                <CheckCircleIcon
                  color={
                    selected ? (theme.palette.type === 'light' ? 'primary' : 'inherit') : 'disabled'
                  }
                />
              </ListItemIcon>
            </ListItem>
          );
        })}
      </List>
    );
  }, [theme, classes.icon, globalState, updatePlaybackRate]);

  useMount(() => {
    // init `playbackRate`
    let playbackRate = localStore.get(PLAYBACK_RATE_STORED_KEY);
    if (!playbackRateCollection.list.includes(String(playbackRate))) {
      playbackRate = DEFAULT_PLAYBACK_RATE;
    }
    updatePlaybackRate(Number(playbackRate));
  });

  return (
    <>
      <IconButton
        edge="start"
        color="inherit"
        onClick={() => {
          setOpen(true);
        }}
      >
        <Badge
          badgeContent={
            pbrEntites[globalState.playbackRate].value === 1
              ? null
              : pbrEntites[globalState.playbackRate].text
          }
          color="primary"
        >
          <SlowMotionVideoIcon />
        </Badge>
      </IconButton>
      <Drawer anchor="bottom" variant="temporary" open={open} onClose={() => setOpen(false)}>
        {listItems}
      </Drawer>
    </>
  );
}
