import { useCallback, useMemo, useState } from 'react';
import { useMount } from 'react-use';
import { useTheme } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Badge from '@material-ui/core/Badge';
import IconButton from '@material-ui/core/IconButton';
import FilterListIcon from '@material-ui/icons/FilterList';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import { convertToEntityList } from '@src/lib';
import {
  coursePhaseCollection,
  DEFAULT_COURSE_PHASE,
  COURSE_PHASE_STORED_KEY,
} from '@src/configs/course';
import { useGlobalState } from '@src/components';
import { localStore } from '@src/utils';

const { entities: coursePhaseEntites } = coursePhaseCollection;

const useStyles = makeStyles(() => ({
  icon: {
    minWidth: 'auto',
  },
}));

export function CourseFilter() {
  const theme = useTheme();
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const [globalState, dispathAction] = useGlobalState();

  const updateCoursePhase = useCallback(
    (/** @type {number} */ coursePhase) => {
      dispathAction({
        type: 'updateCoursePhase',
        payload: { value: coursePhase },
      });
    },
    [dispathAction],
  );

  const listItems = useMemo(() => {
    const coursePhaseEntityList = convertToEntityList(coursePhaseCollection);

    return (
      <List component="div" subheader={<ListSubheader component="div">{'è¯¾ç¨‹æœŸåˆ«'}</ListSubheader>}>
        {coursePhaseEntityList.map(coursePhase => {
          const selected = coursePhase.value === globalState.coursePhase;

          return (
            <ListItem
              key={coursePhase.value}
              button
              selected={selected}
              onClick={() => {
                updateCoursePhase(coursePhase.value);
                setOpen(false);

                if (coursePhase.value !== globalState.coursePhase) {
                  window.scrollTo(0, 0);
                }
              }}
            >
              <ListItemText
                primary={
                  <Typography
                    color={
                      selected
                        ? theme.palette.type === 'light'
                          ? 'primary'
                          : 'textPrimary'
                        : theme.palette.type === 'light'
                        ? 'inherit'
                        : 'textSecondary'
                    }
                  >
                    {coursePhase.text}
                  </Typography>
                }
              />
              <ListItemIcon className={classes.icon}>
                <CheckCircleIcon
                  color={
                    selected ? (theme.palette.type === 'light' ? 'primary' : 'inherit') : 'disabled'
                  }
                />
              </ListItemIcon>
            </ListItem>
          );
        })}
      </List>
    );
  }, [theme, classes.icon, globalState, updateCoursePhase]);

  useMount(() => {
    // init `coursePhase`
    let coursePhase = localStore.get(COURSE_PHASE_STORED_KEY);
    if (!coursePhaseCollection.list.includes(String(coursePhase))) {
      coursePhase = DEFAULT_COURSE_PHASE;
    }
    updateCoursePhase(Number(coursePhase));
  });

  return (
    <>
      <IconButton
        edge="start"
        color="inherit"
        onClick={() => {
          setOpen(true);
        }}
      >
        <Badge badgeContent={coursePhaseEntites[globalState.coursePhase].brief} color="primary">
          <FilterListIcon />
        </Badge>
      </IconButton>
      <Drawer anchor="bottom" variant="temporary" open={open} onClose={() => setOpen(false)}>
        {listItems}
      </Drawer>
    </>
  );
}
